import { Navigate, Route, Routes as RouterRoutes } from 'react-router-dom'
import { HomePage } from 'app/pages/HomePage/Loadable'
import NotFoundPage from 'app/pages/NotFoundPage/Loadable'
import RouteEnum from './RouteEnum'
import DOTSPage from 'app/pages/DOTSPage'

function Routes() {
  return (
    <RouterRoutes>
      <Route path={RouteEnum.HOME.path} element={<HomePage />} />
      <Route path={RouteEnum.DOTS_RECORD.path} element={<DOTSPage />} />

      <Route path="/" element={<Navigate to={RouteEnum.HOME.path} replace />} />
      {/* Default fallback to any unrecognised path */}
      <Route path="*" element={<NotFoundPage />} />
    </RouterRoutes>
  )
}

export default Routes
