import React from 'react';
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
// import Stack from '@mui/material/Stack';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import Select, { SelectChangeEvent } from '@mui/material/Select';
import OutlinedInput from '@mui/material/OutlinedInput';
import { RELATIONSHIP_LIST } from './constants';
import { Typography, Divider } from '@mui/material';
import { StyledDiv } from './PeriodDetails';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};


function ContactDetails(){

    const [selectedRelationship, setSelectRelationship] = React.useState('');

    const handleRelationshipChange = (event: SelectChangeEvent) => {
      setSelectRelationship(event.target.value as string);
    };

    return(
        <StyledDiv>
        <Typography variant='h6' sx={{fontWeight: 'bold'}}>Person to contact in Singapore in case of emergency</Typography>
        <Divider sx={{paddingTop: 1, marginBottom:2}}/>
          <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
            <Grid item xs={12}>
              <InputLabel>Name of Contact Person</InputLabel>
              <TextField type="string" fullWidth />
            </Grid>
          </Grid>
          <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
            <Grid item xs={12}>
              <InputLabel>Relationship</InputLabel>
              <Select
                value={selectedRelationship}
                onChange={handleRelationshipChange}
                input={<OutlinedInput label="Relationship" />}
                MenuProps={MenuProps}
                fullWidth
              >
                {RELATIONSHIP_LIST.map((name) => (
                  <MenuItem
                    key={name}
                    value={name}
                  >
                    {name}
                  </MenuItem>
                ))}
              </Select>
            </Grid>
          </Grid>
          <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
            <Grid item xs={12}>
              <InputLabel>Home / Office Number</InputLabel>
              <TextField type="string" fullWidth />
            </Grid>
          </Grid>
          <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
            <Grid item xs={12}>
              <InputLabel>Mobile Number</InputLabel>
              <TextField type="string" fullWidth />
            </Grid>
          </Grid>

          <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
            <Grid item xs={12}>
              <InputLabel>Alternative Number</InputLabel>
              <TextField type="string" fullWidth />
            </Grid>
          </Grid>
          <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
            <Grid item xs={12}>
              <InputLabel>Residential Address </InputLabel>
              <TextField type="string" fullWidth multiline rows={5} />
            </Grid>
          </Grid>
        </StyledDiv>
    )
}

export default ContactDetails;