
import TextField from '@mui/material/TextField';
import Grid from '@mui/material/Grid';
import InputLabel from '@mui/material/InputLabel';
import { Typography, Divider } from '@mui/material';
import { StyledDiv } from './PeriodDetails';

function AdditionalDetails(){
    return(
      <StyledDiv>
      <Typography variant='h6' sx={{fontWeight: 'bold'}}>Additional Overseas Details</Typography>
      <Divider sx={{paddingTop: 1, marginBottom:2}}/>
        <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
          <Grid item xs={12}>
            <InputLabel>Address in foreign country (including Hotels). Please indicate the city/cities and states</InputLabel>
            <TextField type="string" fullWidth multiline rows={5} />
          </Grid>
        </Grid>
        <Grid container spacing={2} sx={{paddingTop:1, paddingBottom:1}}>
          <Grid item xs={12}>
            <InputLabel>Mobile Contact Number (Local/Overseas)</InputLabel>
            <TextField type="string" fullWidth />
          </Grid>
        </Grid>
        </StyledDiv>
    )
}

export default AdditionalDetails;